<template>
  <button @click="onClick()" :style="{background: color}" class="btn">{{text}}</button>
</template>


<script>
export default {
  name: 'Button',
  props: {
    text: String,
    color: String
  },
  methods: {
    onClick() {
      this.$emit('btn-click')
    }
  }
}

</script>

<style scoped>
@media all and (max-width: 400px) {
    button {
      font-size: inherit;
    }
}

</style>